import React, {useState, useEffect, useCallback} from 'react'
import {View, ScrollView, Text, Button, StyleSheet} from 'react-native'
import Background from './Background'
import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons'
import Navbar from './Navbar'
import TextInput from './TextInput'
import { TouchableOpacity, Image } from 'react-native';
import SendIcon from '@mui/icons-material/Send';
import './Chat.js';

import firebase from 'firebase/app';

import { storage, store, authenticate } from "../App.js";
const convo = String(Math.floor(Math.random()*1000000000000000))

export default function ChatScreen({ navigation }) {
  const [message, setMessage] = useState([]);
  const [messages, setMessages] = useState([])
  useEffect(() => {
      store.collection('messages').doc(convo).collection('convo').orderBy('createdAt').limit(50).onSnapshot(snapshot => {
          setMessages(snapshot.docs.map(doc => doc.data()))
      })
  }, [])

   async function sendMessage(e) {
        e.preventDefault()
        const uid = authenticate.currentUser.uid
        const avatar = authenticate.currentUser.photoURL

        if (message.length > 0){
          await store.collection('messages').doc(convo).set({
              createdAt: firebase.firestore.FieldValue.serverTimestamp()
          })

          await store.collection('messages').doc(convo).collection('convo').add({
              text: message,
              avatar,
              uid,
              createdAt: firebase.firestore.FieldValue.serverTimestamp()
          })
        }
        setMessage('')
    }

  class StyleSheets {
    static create(obj) {
      var result = {};
      for (var key in obj) {
        result[key] = obj[key]
      }
      return result;
    }
  }  

  const styles = StyleSheets.create({
    msg: {
      display: 'flex',
      padding: '0px 10px 0 10px',
      margin: '5px',
      alignItems: 'center',
      color: 'white',
      backgroundColor: 'blue',
      width: 'min-content',
    },
  });

  return (
    <Background>
     <ScrollView>
      <Navbar />
      <div className="container">
        <div className="contacts">
        </div>
        <div className="msgs">
              {messages.map(({ id, text, photoURL, uid }) => (
                  <div>
                      <div key={id} style={styles.msg} className={`msg ${uid === authenticate.currentUser.uid ? 'sent' : 'received'}`}>
                          <img src={photoURL} alt="" />
                          <p>{text}</p>
                      </div>
                  </div>
              ))}
        </div>
      </div>
      <TextInput 
       style={{ width: '78%', fontSize: '15px', fontWeight: '550', marginLeft: '5px', marginBottom: '-3px' }}
       theme={{ colors: { primary: 'blue',underlineColor:'transparent',}}}
       placeholder='Message...'
       value={message}
       onChangeText={e => setMessage(e)} />
      <TouchableOpacity onPress={sendMessage}>
        <SendIcon />
      </TouchableOpacity>
      </ScrollView>
    </Background>
  )
}

